import { mapState } from "vuex";
import cookies from "vue-cookies";
export default {
  props: {
    food: {
      type: Object,
    },
  },
  computed: {
    ...mapState({
      collectionList: (state) => state.collectionList,
    }),
    collectionIdList() {
      return this.$store.getters.collection_id_list;
    },
    if_collection() {
      return this.collectionIdList.includes(this.food.food_id);
    },
  },
  methods: {
    confirmLogin() {
      let res = confirm("Please login your account.");
      if (res == true) {
        this.$router.push("/login");
      }
    },
    async removeCollection() {
      if (cookies.get("token")) {
        await this.$axios({
          url: "/api/collection",
          method: "delete",
          data: {
            token: cookies.get("token"),
            food_id: this.food.food_id,
          },
        })
          .then((res) => {
            console.log(res.data);
            this.$store.commit("removeCollectionList", this.food.food_id);
          })
          .catch((err) => {
            alert(err);
          });
      } else {
        this.confirmLogin();
      }
    },
    async addCollection() {
      if (cookies.get("token")) {
        await this.$axios({
          url: "/api/collection",
          method: "post",
          data: {
            token: cookies.get("token"),
            food_id: this.food.food_id,
          },
        })
          .then((res) => {
            console.log(res.data);
            this.$store.commit("addCollectionList", this.food);
          })
          .catch((err) => {
            alert(err);
          });
      } else {
        this.confirmLogin();
      }
    },
  },
};
